<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">采购中心</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>采购商品详情</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
      <el-form :inline="true" label-width="110px" label-position="right" :model="from" class="demo-form-inline" style="margin:20px;">
        <el-form-item label="商品图片:">
          <img :src="from.goods_image" alt="" style="width:100px;height:100px" srcset="">
        </el-form-item>
        <br>
        <el-form-item label="商品名称:">
          {{from.goods_name}}
        </el-form-item>
        <br>

        <el-form-item label="商品品牌:">
          {{from.brand_name}}
        </el-form-item>
        <br>

        <el-form-item label="库存:">
          {{from.goods_storage}}
        </el-form-item>
        <br>

        <el-form-item label="阶梯价:">
          <div v-for="(item,index) in from.sourcing_price" :key="index">
            <span>数量：{{item.minimum_number}}——{{item.maximum_number}}件</span>
            <span>售价:¥{{item.sourcing_price}}</span>
          </div>
        </el-form-item>
        <br>

        <el-form-item label="商品属性:">
          <div v-for="(item,index) in from.attr_list" :key="index">
            <span>{{item.attr_name}}：{{item.attr_value}}</span>
          </div>
        </el-form-item>
        <br>

        <el-form-item label="所在地:">
          {{from.area_name}}
        </el-form-item>
        <br>

        <el-form-item label="适配车型:" v-if='showModelsName'>
          {{showModelsName}}
          <el-button type='primary' @click="isShowFairModel = true">查看全部</el-button>
        </el-form-item>
        <br>

        <el-form-item label="品牌件码/OE码:" v-if="from.oe_code">
          {{from.oe_code}}
        </el-form-item>
      </el-form>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返回列表</el-button>
    </div>

    <modelDetail v-model='isShowFairModel' ref='modelDetail' @getOeCode='(val) => { from.oe_code = val }' :library_id='from.library_id' />
  </div>
</template>

<script>
import modelDetail from "common/components/plugin/service/modelDetail.vue";
export default {
  data() {
    return {
      from: {},
      isShowFairModel: false,
      showModelsName: "",
    };
  },
  methods: {
    goBack() {
      this.$router.push({
        path: "/purchase/jybdorder",
      });
    },
    getGoodsInfo(Param) {
      this.$axMethod.jybdgetGoodsInfo(Param).then(({ data }) => {
        if (data.code == 200) {
          this.from = data.data;
          this.$nextTick(() => {
            this.$refs.modelDetail.getData();
          });
        }
      });
    },
  },
  components: {
    modelDetail,
  },
  mounted() {
    let goods_id = this.$route.query.shop_goods_id || 0;
    this.getGoodsInfo({ shop_goods_id: goods_id });
  },
};
</script>

<style lang="less" scoped>
</style>
